import React, { Fragment } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { hideInfo } from './../actions';

import clickFile from './../sound/click.mp3';
let clickSound = new Audio(clickFile);

const Info = () => {
    let soundState = useSelector(state => state.ui.playSound);
    let dispatch = useDispatch();
    return (
        <Fragment>
            <h2 className='info-title'>About this Site</h2>
            
            <p className='info-p'>My name is Isaac Sheets and I developed Complete-a-Tweet 2000 as a commentary on the usage of infinite scroll on Twitter. Infinite scroll provides easy access to unlimited content and the user is never forced to take a step back and decide if they want to continue - the content is easy to get so it has less weight.</p>
            <p className='info-p'>To reverse this concept, Complete-a-Tweet 2000 slows down the interaction for the user and prompts them to actually engage with the content they see. The result is that the user can make a more intentional decision about the content the consume and the time they invest.</p>
            <a className='info-link' target='_blank' rel="noopener noreferrer" href='http://creative.colorado.edu/~issh3780/'>&#x2197; Check out my other work</a>
            <a className='info-link' target='_blank' rel="noopener noreferrer" href='https://www.linkedin.com/in/isaacsheets1'>&#x2197; Find me on LinkedIn</a>
            <button className='small-text info-close-button' onClick={() => {
                if(soundState === true) {
                    clickSound.play();
                }
                dispatch(hideInfo())
                }}>CLOSE</button>
        </Fragment>
    )
}

export default Info;